<template>
	<view>
		<u-swiper :list="slides" name="img_url" height="320"></u-swiper>
		<view class="u-text-center u-m-t-30">
			<u-tabs :list="sortList" bar-width="100" item-width="150" :current="currentSort" @change="changeSort">
			</u-tabs>
		</view>

		<u-row gutter="16">
			<u-col span="6" v-for="item in goods">
				<navigator class="goods-items">
					<u-image width="100%" height="300rpx" :src="item.cover_url"></u-image>
					<view class="title u-line-1">{{item.title}}</view>
					<view class="u-flex u-row-between">
						<view class="price">￥ {{item.price}}</view>
						<view class="sales">销量：{{item.sales}}</view>
					</view>
				</navigator>
			</u-col>
		</u-row>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				sortList: [{
						name: '默认'
					},
					{
						name: '销量'
					},
					{
						name: '推荐'
					},
					{
						name: '最新'
					}
				],
				currentSort: 0,
				slides: [],
				goods: [],
				page: 1,
			}
		},
		onLoad() {
			this.getData()
		},
		methods: {
			changeSort(index) {
				this.currentSort = index
			},
			// 获取数据
			async getData(){
				const params = {
					page:this.page
				}
				const res = await this.$u.api.index(params)
				this.slides = res.slides
				this.goods = [...this.goods,...res.goods.data]
			},
		},
		onReachBottom() {
			// 重新请求数据，带上分页参数
			this.page = this.page + 1
			this.getData();
		}
	}
</script>

<style lang="scss" scoped>
	.goods-items {
		padding: 40rpx;
		box-shadow: 0 10rpx 15rpx 0 rgba(0, 0, 0, .1);
		margin-top: 30rpx;

		.title {
			margin: 10rpx 0;
			font-weight: 500;
			font-size: 32rpx;
		}

		.price {
			color: red;
		}

		.sales {
			color: #888;
		}
	}
</style>
